// /* Basscss Color Base */
body {
    color: $dark-gray;
    background-color: white;
}

a {
    color: $blue;
    text-decoration: none;
}

a:hover {
    text-decoration: underline;
}

hr {
    border: 0;
    border-bottom-style: solid;
    border-bottom-width: $border-width;
    border-bottom-color: $border-color;
}
