.button-blue {
    color: white;
    background-color: $blue;
    border-radius: $border-radius;
    transition-duration: 0.1s;
    transition-timing-function: ease-out;
    transition-property: box-shadow, background-color;
}

.button-blue:hover {
    opacity: 0.875;
}

.button-blue.is-active,
.button-blue:active {
    box-shadow: inset 0 0 0 32px rgba(#000, 0.125), inset 0 2px 3px 0 rgba(#000, 0.25);
}

.button-blue:focus {
    outline: none;
    box-shadow: 0 0 0 2px rgba(white, 0.5), 0 0 1px 4px rgba($blue, 0.5);
}

.button-blue.is-disabled,
.button-blue:disabled {
    opacity: 0.5;
}
